import React, { useRef, useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import JSONP from 'browser-jsonp';
import { selectScenarioStr } from '../../app/reducer';
import d3plot from './PlotD3';

const useStyles = makeStyles({
  container: {
    position: 'absolute',
    top: 25,
    left: 300,
    display: 'flex',
    flexWrap: 'wrap'
  },
  unreal: {
    fontSize: 35,
    textAlign: 'center',
    padding: 60,
    color: '#888'
  }
});

export const PlotD3Component = ({ data, currentData, width, height }) => {
  const d3Container = useRef(null);
  const dispatch = useDispatch();

  // TODO: https://stackoverflow.com/questions/53446020/how-to-compare-oldvalues-and-newvalues-on-react-hooks-useeffect
  useEffect(() => {
    if (data && d3Container.current) {
      // d3Container.current.innerHTML = '';
      d3plot(d3Container.current, data, currentData, width, height, dispatch);
    }
  }, [data, currentData, dispatch]);

  return (
    <div
      className="d3-component"
      // style={{ width, height, position: 'absolute', left: width }}
      ref={d3Container}
    />
  );
};

