import React, { useState, useEffect } from 'react';
// import logo from './logo.svg';
import { Container } from './features/container/Container';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import SimpleReactLightbox from 'simple-react-lightbox'
import JSONP from 'browser-jsonp';
import { useDispatch } from 'react-redux';
import { setData, setScenario } from './app/reducer';
import './App.css';

const theme = createMuiTheme({
  typography: {
    fontFamily: 'Muli, Arial',
  },
  palette: {
    primary: {
      main: '#f69436',
      contrastText: '#fff'
    },
    secondary: {
      main: '#652f8c',
      contrastText: '#000'
    },
  },
  tablecell: {
    fontSize: '10pt'
  }
});

function App() {
  const [dataLoading, setDataLoading] = useState(false);
  const [dataLoaded, setDataLoaded] = useState(false);
  const [hasError, setHasError] = useState(false);
  const [data, setAppData] = useState(undefined);
  const dispatch = useDispatch();

  window.__loadData__ = (data) => {
    // console.log(data);

    const scenario = {};
    data.design.forEach(d => {
      scenario[d.id] = d.vals[0].index;
    });

    dispatch(setScenario(scenario));
    dispatch(setData(data));

    setAppData(data);
    setDataLoading(false);
    setDataLoaded(true);
  };

  useEffect(() => {
    setDataLoading(true)
    setDataLoaded(false);
    JSONP({
      url: 'data.jsonp',
      callbackName: '__loadData__',
      error: (err) => {
        // enqueueSnackbar(`Couldn't load config: ${err.url}`, { variant: 'error' });
        setHasError(true);
        setDataLoading(false);
      }
    });
  }, []);

  if (dataLoaded) {
    return (
      <SimpleReactLightbox>
        <ThemeProvider theme={theme}>
          <div className="App">
            <Container data={data} />
          </div>
        </ThemeProvider>
      </SimpleReactLightbox>
    );  
  }

  return '';
}

export default App;
