import React, { useRef, useEffect } from 'react';
import { useSelector } from 'react-redux';
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import DollarInput from './DollarInput';
import Counter from './Counter';
import { selectScenarioStr } from '../../app/reducer';
import utility from './utilityFunc';

const useStyles = makeStyles((theme) => ({
  container: {
    position: 'absolute',
    top: 20,
    right: 0,
    padding: 10,
    paddingRight: 25,
    boxSizing: 'border-box',
    background: '#fafafa'
  },
  header: {
    fontSize: 18,
    fontWeight: 600,
    paddingBottom: 25
  },
  timeInfo: {
    fontSize: 14,
    color: '#333',
    paddingLeft: 5
  },
  controlContainer: {
    paddingTop: 5,
    paddingBottom: 5,
    paddingLeft: 5
  },
  textFieldRoot: {
    '& > *': {
      margin: theme.spacing(1),
    }
  },
  stats: {
    paddingLeft: 8,
    paddingTop: 10,
    fontSize: 16,
    color: 'gray',
    textAlign: 'right'
  },
  statsCounter: {
    fontSize: 24
  },
  decisionContainer: {
    paddingLeft: 8,
    paddingTop: 15,
    textAlign: 'right'
  },
  go: {
    fontSize: 25,
    color: 'green'
  },
  nogo: {
    fontSize: 25,
    color: 'red'
  }
}));

const currencyFormat = d => {
  const sign = d < 0 ? "-" : "";
  const num = Number(parseFloat(Math.abs(d)).toFixed(2)).toLocaleString('en');
  return `${sign}$${num}`;
}

export function Utility({
  data, width, height
}) {
  const classes = useStyles();

  const scenario = useSelector(selectScenarioStr);

  // const dispatch = useDispatch();

  const usePrevious = (value) => {
    const ref = useRef();
    useEffect(() => {
      ref.current = value;
    });
    return ref.current;
  }

  const [Cs, setCs] = React.useState(500000);
  const handleCsChange = (event) => {
    setCs(event.target.value);
  };

  const [Cm, setCm] = React.useState(500000);
  const handleCmChange = (event) => {
    setCm(event.target.value);
  };

  const [Cp, setCp] = React.useState(50000);
  const handleCpChange = (event) => {
    setCp(event.target.value);
  };

  const [Co, setCo] = React.useState(0);
  const handleCoChange = (event) => {
    setCo(event.target.value);
  };

  const [P, setP] = React.useState(1000000000);
  const handlePChange = (event) => {
    setP(event.target.value);
  };

  // const currentData = useSelector(selectCurrentData);
  // console.log(currentData)

  let N = NaN;
  let TD = NaN;
  let power = NaN;
  let ns = NaN;
  if (data && data.scen_stats[scenario]) {
    N = data.scen_stats[scenario].design_n_tot;
    TD = data.scen_stats[scenario].eos_time_mean;
    power = data.scen_stats[scenario].power;
    ns = data.scen_stats[scenario].design_nsite;
  }

  const util = utility(N, ns, TD, Cs / 1e6, Cm / 1e6, Cp / 1e6, Co / 1e6, P / 1e6, power);
  const prevUtil = utility(usePrevious(N), usePrevious(ns), usePrevious(TD), usePrevious(Cs) / 1e6, usePrevious(Cm) / 1e6, usePrevious(Cp) / 1e6, usePrevious(Co) / 1e6, usePrevious(P) / 1e6, usePrevious(power));

  // console.log(prevUtil.U1)
  // console.log(util.U1)

  // ns,   // S:number of sites
  // Cs,   // User:site start-up cost in million (per site) (1-10)
  // Cm,   // User:site maintaining cost in million (per site per month)
  // Cp,   // User:total cost per patient in million (.001-.035)
  // Co,   // User:other cost in million
  // P,    // User:profit in million
  // Cs = 0.5 1.0
  // Cm = 0.5 1.0
  // Cp = 0.05
  // cost.ns=20,Cs=0.5,Cm=0.5,Cp=0.05,P=1000 

  let et = '';
  let st = '';
  if (data && data.scen_stats[scenario]) {
    const dd = data.scen_stats[scenario];
    et = `${Math.round(dd.acc_time_mean * 10) / 10} (${Math.round(dd.acc_time_lb95_2_5_ * 10) / 10}, ${Math.round(dd.acc_time_ub95_97_5_ * 10) / 10})`;
    st = `${Math.round(dd.eos_time_mean * 10) / 10} (${Math.round(dd.eos_time_lb95_2_5_ * 10) / 10}, ${Math.round(dd.eos_time_ub95_97_5_ * 10) / 10})`;
  }

  return (
    <div className={classes.container} style={{ height, width }}>
      <div className={classes.header}>Utility Calculator</div>
      <div className={classes.timeInfo}>
        {`Enrollment time: ${et}`}
      </div>
      <div className={classes.timeInfo}>
        {`Total study time: ${st}`}
        </div>
      <DollarInput
        label="Site start-up cost (per site)"
        val={Cs}
        handleChange={handleCsChange}
      />
      <DollarInput
        label="Site maintenance cost (per site per month)"
        val={Cm}
        handleChange={handleCmChange}
      />
      <DollarInput
        label="Total cost per patient"
        val={Cp}
        handleChange={handleCpChange}
      />
      <DollarInput
        label="Other costs"
        val={Co}
        handleChange={handleCoChange}
      />
      <DollarInput
        label="Revenue"
        val={P}
        handleChange={handlePChange}
      />
      {data && data.scen_stats[scenario] && (
        <div>
          <div className={classes.stats}>
            <div>Success probability</div>
            <div className={classes.statsCounter}>
              <Counter
                start={isNaN(prevUtil.power) ? 0 : prevUtil.power}
                end={util.power}
                preserveValue
                duration={1}
                decimals={3}
                // formattingFn={currencyFormat}
              />
            </div>
          </div>
          <div className={classes.stats}>
            <div>No-go utility</div>
            <div className={classes.statsCounter}>
              <Counter
                start={isNaN(prevUtil.U0) ? 0 : prevUtil.U0 * 1e6}
                end={util.U0 * 1e6}
                preserveValue
                duration={1}
                formattingFn={currencyFormat}
              />
            </div>
          </div>
          <div className={classes.stats}>
            <div>Go utility</div>
            <div className={classes.statsCounter}>
              <Counter
                start={isNaN(prevUtil.U1) ? 0 : prevUtil.U1 * 1e6}
                end={util.U1 * 1e6}
                preserveValue
                duration={1}
                formattingFn={currencyFormat}
              />
            </div>
          </div>
          <div className={classes.stats}>
            <div>Cost</div>
            <div className={classes.statsCounter}>
              <Counter
                start={isNaN(prevUtil.C) ? 0 : prevUtil.C * 1e6}
                end={util.C * 1e6}
                preserveValue
                duration={1}
                formattingFn={currencyFormat}
              />
            </div>
          </div>
          <div className={classes.stats}>
            <div>Profit</div>
            <div className={classes.statsCounter}>
              <Counter
                start={isNaN(prevUtil.price) ? 0 : prevUtil.price * 1e6}
                end={util.price * 1e6}
                preserveValue
                duration={1}
                formattingFn={currencyFormat}
              />
            </div>
          </div>
          <div className={classes.decisionContainer}>
            {/* <div>{`Power: ${util.power}`}</div> */}
            {/* <div>{`Power cutoff: ${Math.round(util.cutoff * 1000) / 1000}`}</div> */}
            <div>
              <span>Decision: </span>
              <span className={util.power > util.cutoff & util.U1 > 0 ? classes.go : classes.nogo }>
                {util.power > util.cutoff & util.U1 > 0 ? 'Go' : 'No Go'}
              </span>
            </div>
          </div>
          <div>
          </div>
        </div>
      )}
    </div>
  );
};
