import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import Slider from '@material-ui/core/Slider';
import { makeStyles } from '@material-ui/core/styles';
import { Divider } from '@material-ui/core';
import { selectScenario, setScenarioItem } from '../../app/reducer';
import { Propbar } from './Propbar';

const useStyles = makeStyles({
  container: {
    position: 'absolute',
    top: 20,
    left: 0,
    padding: 10,
    boxSizing: 'border-box',
    background: '#fafafa'
  },
  header: {
    fontSize: 18,
    fontWeight: 600,
    paddingBottom: 10
  },
  headerSub: {
    color: 'gray',
    fontSize: 14
  },
  controlContainer: {
    paddingTop: 5,
    paddingBottom: 5,
    paddingLeft: 5
  },
  scenarioHeader: {
    background: 'darkgray',
    color: 'white',
    paddingTop: 3,
    paddingBottom: 3,
    paddingLeft: 10,
    marginTop: 5,
    marginBottom: 5
  },
  valueLabel: {
    color: 'white'
  }
});

export default function PropInput({ d }) {
  const classes = useStyles();
  const scenario = useSelector(selectScenario);
  const dispatch = useDispatch();

  const handleChange = (event) => {
    dispatch(setScenarioItem({
      id: event.target.name,
      value: parseInt(event.target.value)
    }));
  };

  const valLookup = {};
  d.vals.forEach(el => {
    valLookup[el.value] = el.index;
  });

  const handleChange2 = (event, value, id) => {
    dispatch(setScenarioItem({
      id,
      value: valLookup[value]
    }));
  };

  if (d.vals.length <= 3) {
    return (
      <div className={classes.controlContainer}>
        <FormControl component="fieldset">
          <FormLabel component="legend">{d.desc}</FormLabel>
          <RadioGroup row={d.id !== 'subgrpp'} aria-label={d.id} name={d.id} value={scenario[d.id]} onChange={handleChange}>
            {d.vals.map(v => {
              let lbl = v.value;
              if (d.id === 'subgrpp') {
                lbl = (<Propbar val={v.value} width={240} height={28} />);
              }
              return (
                <FormControlLabel value={v.index} control={<Radio size="small" />} label={lbl} key={v.index} />
              );
            })}
          </RadioGroup>
        </FormControl>
        <Divider />
      </div>
    );  
  } else {
    const marks = d.vals.map((aa, i) => {
      const lb = d.vals.length > 5 && i % 2 ? '' : aa.value
      return { value: aa.value, label: lb };
    });
    return (
      <div className={classes.controlContainer}>
        <FormControl component="fieldset">
          <FormLabel component="legend">{d.desc}</FormLabel>
        </FormControl>
          <div style={{width: 240, paddingLeft: 10 }}>
            <Slider
              defaultValue={d.vals[0].value}
              step={null}
              min={d.vals[0].value}
              max={d.vals[d.vals.length - 1].value}
              valueLabelDisplay="auto"
              color="secondary"
              marks={marks}
              track={false}
              onChangeCommitted={(event, value) => handleChange2(event, value, d.id)}
              // classes={{ valueLabel: classes.valueLabel }}
            />
          </div>
        <Divider />
      </div>
    );
  }
};
