import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { ReactComponent as Logo } from './presagia_logo_color.svg'
import { Sidebar } from '../sidebar/Sidebar';
import { Plot } from '../plots/Plot';
import Legend from '../plots/Legend';
import { Utility } from '../utility/Utility';
import { Context } from '../context/Context';
import { selectTab, setTab } from '../../app/reducer';

const useStyles = makeStyles({
  outer: {
    flexGrow: 1
  },
  container: {
    position: 'absolute',
    width: '100%',
    display: 'flex',
    top: 36
  },
  logoDiv: {
    position: 'absolute',
    right: 0,
    top: 0,
    padding: 5
  },
  logo: {
    height: 25 * 1.2,
    width: 91 * 1.2,
    paddingTop: 5,
    paddingRight: 10
  },
  title: {
    flexGrow: 1,
    lineHeight: '50px',
    paddingLeft: 30,
    fontSize: 20,
    fontWeight: 500
  },
  appBar: {
    height: 50
  },
  legend: {
    position: 'absolute',
    bottom: -10,
    left: 315
  },
  tabContainer: {
    position: 'fixed',
    top: 2,
    left: 300,
    zIndex: 1000
  }
});

export function Container({ data }) {
  const classes = useStyles();
  const size = useWindowSize();
  const currentTab = useSelector(selectTab);
  const dispatch = useDispatch();

  const handleTabChange = (event, newValue) => {
    dispatch(setTab(newValue));
  };

  return (
    <div className={classes.outer} style={{ width: size.width }}>
      <AppBar position="static" color="inherit" classes={{ root: classes.appBar }}>
          <div className={classes.title}>
              Phase III Trial Simulator
          </div>
          <div className={classes.tabContainer}>
            <Tabs
              value={currentTab}
              onChange={handleTabChange}
            >
              <Tab label="Background" />
              <Tab label="Simulations" />
            </Tabs>
          </div>
          <div className={classes.logoDiv}><Logo className={classes.logo} /></div>
      </AppBar>
      <div className={classes.container} style={{ width: size.width, height: size.height - 50 }}>
        {currentTab === 0 && (
          <Context data={data} width={size.width} />
        )}
        {currentTab === 1 && (
          <div>
            <Sidebar data={data} width={300} height={size.height - 20} />
            <Plot data={data} width={size.width - 575} height={size.height - 55} />
            <Utility data={data} width={275} height={size.height - 20} />
            <div className={classes.legend}><Legend /></div>
          </div>
        )}
      </div>
    </div>
  );
}

function useWindowSize() {
  const isWindowClient = typeof window === "object";

  const [windowSize, setWindowSize] = useState({
    width: isWindowClient ? window.innerWidth : undefined,
    height: isWindowClient ? window.innerHeight : undefined
  });

  useEffect(() => {
    //a handler which will be called on change of the screen resize
    function setSize() {
      setWindowSize({width: window.innerWidth, height: window.innerHeight });
    }

    if (isWindowClient) {
      // register the window resize listener
      window.addEventListener("resize", setSize);

      // un-register the listener
      return () => window.removeEventListener("resize", setSize);
    }
  }, [isWindowClient, setWindowSize]);

  return windowSize;
}

