import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
  container: {
    display: 'flex'
  },
  bar: {
    overflow: 'hidden',
    whiteSpace: 'nowrap',
    fontSize: 10,
    paddingTop: 3,
    boxSizing: 'border-box',
    lineHeight: '11px',
    textAlign: 'center',
    color: 'white'
    // textOverflow: 'ellipsis'
  }
});

const colors = [
  '#4E79A7',
  '#F28E2B',
  '#E15759',
  '#76B7B2',
  '#59A14F',
  '#EDC948',
  '#B07AA1',
  '#FF9DA7',
  '#9C755F',
  '#BAB0AC'
];

export function Propbar({
  val, width, height
}) {
  const classes = useStyles();

  return (
    <div className={classes.container} style={{ height }}>
      {val.split(',').map((d, i) => {
        const tmp = d.split(' = ');
        const w = width * parseFloat(tmp[1]);
        return (
          <div
            key={tmp[0]}
            className={classes.bar}
            style={{ height, width: w, background: colors[i]}}>
            {tmp[0]}<br/>{tmp[1]}
          </div>
        );
      })}
    </div>
  );
};
