export default function utility(
  N,    // S:total number of patients 
  ns,   // S:number of sites
  TD,   // Sim:total duration of the study
  Cs,   // User:site start-up cost in million (per site) (1-10)
  Cm,   // User:site maintaining cost in million (per site per month)
  Cp,   // User:total cost per patient in million (.001-.035)
  Co,   // User:other cost in million
  P,    // User:profit in million
  power // Sim: power
) {
  // TD = N / ns / accRs + L  // total duration (month) of the study
  const C = (ns * Cs) + (ns * Cm * TD) + (N * Cp) + Co // total cost

  let cutoff = 1;
  if (P > C / 2) {
    cutoff = C / ((2 * P) - C) // decision go if power > cutoff
  }

  const U1 = (power * (P - C)) + ((1 - power) * (-C))
  const U0 = power * (C - P)
  const price = P - C;

  return ({
      U0, U1, cutoff, C, price, power
  });
}
