import { select } from 'd3-selection';
import { scaleLinear } from 'd3-scale';
import { axisBottom, axisLeft } from 'd3-axis';
import { line, area, curveStepAfter } from 'd3-shape';
import 'd3-transition';

const dur = 1000;

export default function d3plot(ref, data, currentData, width, height, dispatch) {
  const div = select(ref);

  const subWidth = width / 3;
  const subHeight = (height - 30) / 2;

  let svg;
  let newSvg = true;
  if (div.select("svg").nodes().length === 0) {
    svg = div.append("svg");
  } else {
    newSvg = false;
    svg = div.select("svg");
  }

  const margin = ({top: 30, right: 5, bottom: 40, left: 30})

  // tooltip
  let ttDiv;

  const mouseover = (d, ttDiv, x, y) => {
    ttDiv.transition()
      .duration(200)
      .style("opacity", .9);
    ttDiv.html(d.time)
      .style("left", (x + 15) + "px")
      .style("top", y + "px");
      // .style("left", (d3.event.pageX) + "px")
      // .style("top", (d3.event.pageY - 28) + "px");
  };

  const mouseleave = (d, ttDiv) => {
    ttDiv.transition()
      .duration(200)
      .style("opacity", 0);
    ttDiv.html("")
  };
  
  if (newSvg) {
    svg = svg
      .attr("width", width)
      .attr("height", height)

    ttDiv = div.append("div")
    .attr("class", "tooltip")
    .attr("id", "tooltip")
    .style("opacity", 0);
      
    // svg.attr("viewBox", [0, 0, width, height])
    //   .style("overflow", "visible");

    const scales = {};

    Object.keys(data.plot_info).forEach(kk => {
      const pi = data.plot_info[kk];
      const leftPad = subWidth * (pi.col - 1);
      const topPad = subHeight * (pi.row - 1);

      const x = scaleLinear()
        .domain([data.x_range[kk][0] - 1, data.x_range[kk][1] + 1])
        .range([leftPad + margin.left, leftPad + subWidth - margin.right]);

      const y = scaleLinear()
        .domain(pi.range).nice()
        .range([topPad + subHeight - margin.bottom, topPad + margin.top])

      let xAxis
      // if (pi.row < 2) {
      xAxis = g => g
        .attr("transform", `translate(0,${topPad + subHeight - margin.bottom})`)
        .call(axisBottom(x).ticks(subWidth / 80).tickSizeOuter(0))
      // } else {
      //   xAxis = g => g
      //   .attr("transform", `translate(0,${topPad + subHeight - margin.bottom})`)
      //   .call(axisBottom(x).ticks(subWidth / 80).tickSizeOuter(0))
      // }

      const yAxis = g => g
        .attr("transform", `translate(${leftPad + margin.left},0)`)
        .call(axisLeft(y).ticks(subHeight / 50))
        // .call(g => g.select(".domain").remove())
        // .call(g => g.select(".tick:last-of-type text").clone()
        //   .attr("x", 3)
        //   .attr("text-anchor", "start")
        //   .attr("font-weight", "bold")
        //   .text(data.y))

      const drawLine = line()
        .defined(d => !isNaN(d.y) && d.y !== null)
        .x(d => x(d.time))
        .y(d => y(d.y))

      const drawLineKM = line()
        .curve(curveStepAfter)
        .defined(d => !isNaN(d.y) && d.y !== null)
        .x(d => x(d.time))
        .y(d => y(d.y))

      const drawArea = area()
        .defined(d => !isNaN(d.lower) && d.lower !== null && !isNaN(d.upper) && d.upper !== null)
        .x(d => x(d.time))
        .y0(d => y(d.lower))
        .y1(d => y(d.upper))

      const drawAreaKM = area()
        .curve(curveStepAfter)
        .defined(d => !isNaN(d.lower) && d.lower !== null && !isNaN(d.upper) && d.upper !== null)
        .x(d => x(d.time))
        .y0(d => y(d.lower))
        .y1(d => y(d.upper))

      scales[kk] = { x, y, drawLine, drawLineKM, drawArea, drawAreaKM };

      svg.append("text")
        .attr("transform", `translate(${leftPad + margin.left + (subWidth / 2) - 10},${topPad + subHeight + margin.top - margin.bottom})`)
        .style("text-anchor", "middle")
        .attr("class", "x-lab")
        .text(kk === 'allkms' ? 'Follow-up time (month)' : 'Study time (month)');

      const g = svg.append("g");

      g.append("path")
        .datum(data.envl[kk])
        .attr("class", `envelope`)
        .attr("d", scales[kk].drawArea)

      svg.append("g")
        .call(xAxis);
  
      svg.append("g")
        .call(yAxis);
    });

    const cd = currentData.plot_data;
    const g = svg.append("g");

    Object.keys(cd).forEach(cdk => {
      const d = cd[cdk];
      if (d.type === 'spag') {
        g.append("path")
          .datum(d.line)
          .attr("class", `selected-line full-stroke key-${cdk}`)
          .attr("d", scales[cdk].drawLine)
        g.selectAll(".selected-circle")
          .data(d.line.filter(a => !isNaN(a.y) && a.y !== null))
          .enter()
          .append("circle")
          .classed("selected-circles", true)
          .classed("full-fill", true)
          .classed(`key-${cdk}`, true)
          .attr("stroke", "none")
          .attr("cx", d => scales[cdk].x(d.time))
          .attr("cy", d => scales[cdk].y(d.y))
          .attr("r", 3)
          .on("mouseover", d => mouseover(d, ttDiv, scales[cdk].x(d.time), scales[cdk].y(d.y)))
          .on("mouseleave", d => mouseleave(d, ttDiv));
        if (d.ci) {
          g.append("path")
            .datum(d.ci)
            .attr("class", `selected-ci full-fill full-stroke key-${cdk}`)
            .attr("d", scales[cdk].drawArea)
        }
      } else if (d.type === 'spag_by_group') {
        Object.keys(d.line).forEach((dk, i) => {
          g.append("path")
            .datum(d.line[dk])
            .attr("class", `selected-line full-stroke-grp${i} grp-${i} key-${cdk}`)
            .attr("d", scales[cdk].drawLine)
          g.selectAll(".selected-circle")
            .data(d.line[dk].filter(a => !isNaN(a.y) && a.y !== null))
            .enter()
            .append("circle")
            .classed("selected-circles", true)
            .classed(`full-fill-grp${i}`, true)
            .classed(`key-${cdk}`, true)
            .classed(`grp-${i}`, true)
            .attr("stroke", "none")
            .attr("cx", d => scales[cdk].x(d.time))
            .attr("cy", d => scales[cdk].y(d.y))
            .attr("r", 3)
            // .on("mouseover", d => mouseover(d, ttDiv, scales[cdk].x(d.time), scales[cdk].y(d.y)))
            // .on("mouseleave", d => mouseleave(d, ttDiv));  
          if (d.ci[dk]) {
            g.append("path")
              .datum(d.ci[dk])
              .attr("class", `selected-ci full-fill-grp${i} full-stroke-grp${i} grp-${i} key-${cdk}`)
              .attr("d", scales[cdk].drawArea)
          }
        });
      // } else if (d.type === 'spag_lrr') {
      //   Object.keys(d.line).forEach((dk, i) => {
      //     g.append("path")
      //       .datum(d.line[dk])
      //       .attr("class", `selected-line full-stroke-lrr${i} grp-${i} key-${cdk}`)
      //       .attr("d", scales[cdk].drawLine)
      //     g.selectAll("selected-circle")
      //       .data(d.line[dk].filter(a => !isNaN(a.y) && a.y !== null))
      //       .enter()
      //       .append("circle")
      //       .classed("selected-circles", true)
      //       .classed(`full-fill-lrr${i}`, true)
      //       .classed(`key-${cdk}`, true)
      //       .classed(`grp-${i}`, true)
      //       .attr("stroke", "none")
      //       .attr("cx", d => scales[cdk].x(d.time))
      //       .attr("cy", d => scales[cdk].y(d.y))
      //       .attr("r", 2.5)
      //     if (d.ci[dk]) {
      //       g.append("path")
      //         .datum(d.ci[dk])
      //         .attr("class", `selected-ci full-fill-lrr${i} full-stroke-lrr${i} grp-${i} key-${cdk}`)
      //         .attr("d", scales[cdk].drawArea)
      //     }
      //   });
      } else if (d.type === 'km_by_group') {
        Object.keys(d.line).forEach((dk, i) => {
          g.append("path")
            .datum(d.line[dk])
            .attr("class", `selected-line full-stroke-grp${i} grp-${i} key-${cdk}`)
            .attr("d", scales[cdk].drawLineKM);
          if (d.ci[dk]) {
            g.append("path")
              .datum(d.ci[dk])
              .attr("class", `selected-ci full-fill-grp${i} full-stroke-grp${i} grp-${i} key-${cdk}`)
              .attr("d", scales[cdk].drawAreaKM)
          }
        });
      }
    });

    // add plot titles on top
    const pdat = currentData.plot_data;
    Object.keys(pdat).forEach(pdk => {
      svg.append("rect")
        .classed("plot-title-strip", true)
        .attr("y", scales[pdk].y.range()[1] - 25)
        .attr("x", scales[pdk].x(data.x_range[pdk][0]) - 7)
        .attr("width", scales[pdk].x(data.x_range[pdk][1]) - scales[pdk].x(data.x_range[pdk][0]) + 7)
        .attr("height", 25)
        .text(pdat[pdk].ylab);

      svg.append("text")
        .classed("plot-title", true)
        .attr("y", scales[pdk].y.range()[1] - 9)
        .attr("x", scales[pdk].x(data.x_range[pdk][0]) + 4)
        .text(pdat[pdk].ylab);
    });
  } else {
    // update existing plot
    ttDiv = select("#tooltip");

    Object.keys(currentData.plot_data).forEach(cdk => {
      const cd = currentData.plot_data[cdk];
      const pi = data.plot_info[cdk];
      const leftPad = subWidth * (pi.col - 1);
      const topPad = subHeight * (pi.row - 1);
      // const del = ((pi.col - 1) + ((pi.row - 1) * 3)) * 100;
      const del = 0;
  
      const x = scaleLinear()
        .domain([data.x_range[cdk][0] - 1, data.x_range[cdk][1] + 1])
        .range([leftPad + margin.left, leftPad + subWidth - margin.right])
  
      const y = scaleLinear()
        .domain(pi.range).nice()
        .range([topPad + subHeight - margin.bottom, topPad + margin.top])
  
      const drawLine = line()
        .defined(d => !isNaN(d.y) && d.y !== null)
        .x(d => x(d.time))
        .y(d => y(d.y))

      const drawArea = area()
        .defined(d => !isNaN(d.lower) && d.lower !== null && !isNaN(d.upper) && d.upper !== null)
        .x(d => x(d.time))
        .y0(d => y(d.lower))
        .y1(d => y(d.upper))

      const drawLineKM = line()
        .curve(curveStepAfter)
        .defined(d => !isNaN(d.y) && d.y !== null)
        .x(d => x(d.time))
        .y(d => y(d.y))

      const drawAreaKM = area()
        .curve(curveStepAfter)
        .defined(d => !isNaN(d.lower) && d.lower !== null && !isNaN(d.upper) && d.upper !== null)
        .x(d => x(d.time))
        .y0(d => y(d.lower))
        .y1(d => y(d.upper))

      if (cd.type === 'spag') {
        svg.select(`.selected-line.key-${cdk}`)
          .transition().duration(dur).delay(del)
          .attr("d", drawLine(cd.line));
        if (cd.ci) {
          svg.select(`.selected-ci.key-${cdk}`)
            .transition().duration(dur).delay(del)
            .attr("d", drawArea(cd.ci))
        }
        const sel = svg.selectAll(`.selected-circles.key-${cdk}`)
          .data(cd.line.filter(a => !isNaN(a.y) && a.y !== null));
        sel.enter()
          .append("circle")
          .classed("selected-circles", true)
          .classed("full-fill", true)
          .classed(`key-${cdk}`, true)
          .attr("stroke", "none")
          .attr("r", 3)
          .attr("cx", d => x(d.time))
          .attr("cy", d => y(d.y))
        const sel2 = svg.selectAll(`.selected-circles.key-${cdk}`);  
        sel.exit().remove();
        sel2.transition().duration(dur).delay(del)
          .attr("cx", d => x(d.time)).attr("cy", d => y(d.y));
      } else if (cd.type === 'spag_by_group') {
        Object.keys(cd.line).forEach((dk, i) => {
          svg.select(`.selected-line.key-${cdk}.grp-${i}`)
            .transition().duration(dur).delay(del)
            .attr("d", drawLine(cd.line[dk]));
          if (cd.ci[dk]) {
            svg.select(`.selected-ci.key-${cdk}.grp-${i}`)
              .transition().duration(dur).delay(del)
              .attr("d", drawArea(cd.ci[dk]))
          }
          const sel = svg.selectAll(`.selected-circles.key-${cdk}.grp-${i}`)
            .data(cd.line[dk].filter(a => !isNaN(a.y) && a.y !== null));
          sel.enter()
            .append("circle")
            .classed("selected-circles", true)
            .classed(`full-fill-grp${i}`, true)
            .classed(`key-${cdk}`, true)
            .classed(`grp-${i}`, true)
            .attr("stroke", "none")
            .attr("cx", d => x(d.time))
            .attr("cy", d => y(d.y))
            .attr("r", 3)
          const sel2 = svg.selectAll(`.selected-circles.key-${cdk}.grp-${i}`);  
          sel.exit().remove();
          sel2.transition().duration(dur).delay(del)
            .attr("cx", d => x(d.time)).attr("cy", d => y(d.y));
          });
      } else if (cd.type === 'spag_lrr') {
        Object.keys(cd.line).forEach((dk, i) => {
          svg.select(`.selected-line.key-${cdk}.grp-${i}`)
            .transition().duration(dur).delay(del)
            .attr("d", drawLine(cd.line[dk]));
          if (cd.ci[dk]) {
            svg.select(`.selected-ci.key-${cdk}.grp-${i}`)
              .transition().duration(dur).delay(del)
              .attr("d", drawArea(cd.ci[dk]))
          }
          const sel = svg.selectAll(`.selected-circles.key-${cdk}.grp-${i}`)
            .data(cd.line[dk].filter(a => !isNaN(a.y) && a.y !== null));
          sel.exit().remove();
          sel.transition().duration(dur).delay(del)
            .attr("cx", d => x(d.time)).attr("cy", d => y(d.y));
        });
      } else if (cd.type === 'km_by_group') {
        Object.keys(cd.line).forEach((dk, i) => {
          svg.select(`.selected-line.key-${cdk}.grp-${i}`)
            .transition().duration(dur).delay(del)
            .attr("d", drawLineKM(cd.line[dk]));
          if (cd.ci[dk]) {
            svg.select(`.selected-ci.key-${cdk}.grp-${i}`)
              .transition().duration(dur).delay(del)
              .attr("d", drawAreaKM(cd.ci[dk]))
          }
        });

        // Object.keys(d.line).forEach((dk, i) => {
        //   g.append("path")
        //     .datum(d.line[dk])
        //     .attr("class", `selected-line full-stroke-grp${i} key-${cdk}`)
        //     .attr("d", scales[cdk].drawLineKM)
        //   if (d.ci[dk]) {
        //     g.append("path")
        //       .datum(d.ci[dk])
        //       .attr("class", `selected-ci full-fill-grp${i} full-stroke-grp${i} key-${cdk}`)
        //       .attr("d", scales[cdk].drawAreaKM)
        //   }
        // });
      }
    });
  }

  // svg.call(hover, path);
}
