import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import PropInput from './PropInput';
import BetaDialog from './BetaDialog';

const useStyles = makeStyles({
  container: {
    position: 'absolute',
    top: 20,
    left: 0,
    padding: 10,
    paddingTop: 0,
    boxSizing: 'border-box',
    background: '#fafafa'
  },
  header: {
    fontSize: 18,
    fontWeight: 600,
    paddingBottom: 10
  },
  headerSub: {
    color: 'gray',
    fontSize: 14
  },
  controlContainer: {
    paddingTop: 5,
    paddingBottom: 5,
    paddingLeft: 5
  },
  plotContainer: {
    cursor: 'pointer !important'
  },
  scenarioHeader: {
    background: 'darkgray',
    color: 'white',
    paddingTop: 3,
    paddingBottom: 3,
    paddingLeft: 10,
    marginTop: 5,
    marginBottom: 5
  }
});

export function Sidebar({
  data, width, height
}) {
  const classes = useStyles();

  const [betaDialogOpen, setBetaDialogOpen] = React.useState(false);

  // const [initialRender, setInitialRender] = useState(true);
  // const setRendered = () => {
  //   setInitialRender(false);
  // };

  if (data === undefined) {
    return null;
  }

  return (
    <div className={classes.container} style={{ height, width }}>
      <BetaDialog
        open={betaDialogOpen}
        setOpen={setBetaDialogOpen}
        img={data.beta_plot2}
      />
      {/* <div className={classes.header}>Select Scenario</div> */}
      {/* <div className={classes.headerSub}>
        {`${Object.keys(data.data).length} scenarios`}
      </div> */}
      <div>
        {data.design_map.map(dd => (
          <div key={dd.title}>
            <div className={classes.scenarioHeader}>{dd.title}</div>
            {dd.elements.map(ii => (
              <PropInput key={data.design[ii - 1].id} d={data.design[ii - 1]} />
            ))}
          </div>
        ))}
        {data.is_beta && (
          <div className={classes.controlContainer}>
            <FormControl component="fieldset">
              <FormLabel component="legend">
                {"Response rate distribution"}
              </FormLabel>
            </FormControl>
            <img
              className={classes.plotContainer}
              src={data.beta_plot}
              style={{ marginLeft: -5 }}
              width="284"
              onClick={() => setBetaDialogOpen(true)}
            />
          </div>
        )}
      </div>
    </div>
  );
};
