import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import CountUp from 'react-countup';

const useStyles = makeStyles({
  red: {
    color: 'red'
  },
  green: {
    color: 'green'
  },
  black: {
    color: 'black'
  }
});

export default function Counter({
  start, end, delay, duration, formattingFn, decimals
}) {
  const classes = useStyles();
  // console.log(start)
  // console.log(end)

  let nodeRef;

  function onEnd() {
     nodeRef.current.classList.add(classes.black);
  }
  
  return (
    <CountUp
      start={start}
      end={end}
      onEnd={onEnd}
      delay={delay}
      duration={duration}
      formattingFn={formattingFn}
      decimals={decimals}
    >
      {({ countUpRef }) => {
        nodeRef = countUpRef;
        if (nodeRef && nodeRef.current && nodeRef.current.classList) {
          // console.log(nodeRef.current.classList)
          nodeRef.current.classList.remove(classes.black);
        }
        return (
          <span className={end > start ? classes.green : classes.red} ref={countUpRef} />
        );
      }}
    </CountUp>
  );
};

Counter.defaultProps = {
  delay: 0,
  duration: 1,
  formattingFn: d => d,
  prefix: "",
  decimals: 0
}



