import { createSlice, createSelector } from '@reduxjs/toolkit';

export const appSlice = createSlice({
  name: 'app',
  initialState: {
    data: undefined,
    tab: 0,
    selectedScenario: undefined,
    scenarioData: undefined
  },
  reducers: {
    setData: (state, action) => {
      state.data = action.payload;
    },
    setTab: (state, action) => {
      state.tab = action.payload;
    },
    setScenario: (state, action) => {
      state.scenario = action.payload;
    },
    setScenarioItem: (state, action) => {
      state.scenario[action.payload.id] = action.payload.value;
    },
    setScenarioData: (state, action) => {
      state.scenarioData = action.payload;
    }
  }
});

export const {
  setData, setTab, setScenario, setScenarioItem
} = appSlice.actions;

export const selectData = state => state.data;
export const selectTab = state => state.tab;
export const selectScenario = state => state.scenario;

export const selectScenarioStr = createSelector(
  [selectData, selectScenario],
  (data, scenario) => {
    if (data === undefined || scenario === undefined) {
      return '';
    }
    
    return data.design.map(d => scenario[d.id]).join('_');
  }
);

export const selectCurrentData = createSelector(
  [selectData, selectScenarioStr],
  (data, scenarioStr) => {
    if (data === undefined || selectScenarioStr === '') {
      return undefined;
    }

    return data.data[scenarioStr];
  }
);

